import { redirect } from "next/navigation";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Can } from "@/components/auth/can";
import { DeleteButton } from "@/components/delete-button";
import { cookies } from "next/headers"; // <--- NEW

export default async function Dashboard() {
  // 1. Read the Role from Cookie (or default to Admin)
  const cookieStore = await cookies();
  const roleName = cookieStore.get("MOCK_ROLE")?.value || "Admin";
  
  const user = { email: "admin@webzenith.com", id: "offline-user-123" };

  return (
    <div className="p-8">
      <Card>
        <CardHeader>
          <CardTitle>Welcome, {user.email}</CardTitle>
        </CardHeader>
        <CardContent>
          <p className="text-muted-foreground mb-4">
            You have successfully logged in! (Offline Demo Mode)
          </p>
          
          <div className="grid gap-4 md:grid-cols-2">
            <div className="p-4 bg-slate-100 rounded-md border">
              <span className="text-xs font-bold text-slate-500 uppercase">User ID</span>
              <p className="font-mono text-sm truncate">{user.id}</p>
            </div>
            
            <div className="p-4 bg-blue-50 rounded-md border border-blue-100">
              <span className="text-xs font-bold text-blue-500 uppercase">Current Role</span>
              {/* This now updates automatically! */}
              <p className="text-xl font-bold text-blue-700">{roleName}</p>
            </div>
          </div>

          {/* --- RESTRICTED ZONE --- */}
          <div className="mt-6 p-4 border border-red-200 rounded-lg bg-red-50">
            <h3 className="font-bold text-red-900 mb-2">Restricted Action Area</h3>
            <p className="text-sm text-red-700 mb-4">
              The button below only appears if your role has the <strong>'users.delete'</strong> permission.
            </p>
            
            <Can 
              perform="users.delete" 
              fallback={
                <div className="p-4 bg-white/50 rounded border border-dashed border-red-300 text-sm text-red-500 italic flex items-center justify-center gap-2">
                   â›” <strong>Access Denied:</strong> Your role ({roleName}) does not have permission to delete databases.
                </div>
              }
            >
              <DeleteButton />
            </Can>
          </div>
          {/* ----------------------- */}

        </CardContent>
      </Card>
    </div>
  );
}